/* Position and sizing of burger button */
.bm-burger-button {
  position:absolute;
  width: 36px;
  height: 30px;
  left: 10px;
  top: 160px;
  display: none;
}

/* Color/shape of burger icon bars */
.bm-burger-bars {
  background: white;
  display: none;
}

/* Color/shape of burger icon bars on hover*/
.bm-burger-bars-hover {
  background: #B2BB8A;
}
a .bm-burger-bars-hover {
  background: white;
}

/* Position and sizing of clickable cross button */
.bm-cross-button {
  height: 24px;
  width: 24px;
}

/* Color/shape of close button cross */
.bm-cross {
  background: white;
}

/*
Sidebar wrapper styles
Note: Beware of modifying this element as it can break the animations - you should not need to touch it in most cases
*/
.bm-menu-wrap {
  position: fixed;
  height: 100vh;
  overflow-y: hidden;
}

/* General sidebar styles */
.bm-menu {
  background: #B2BB8A;
  padding: 2.5em 1em 0;
  font-size: 1.15em;
}

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
  fill: #373a47;
}

/* Wrapper for item list */
.bm-item-list {
  color: #b8b7ad;
  display: flex;
  flex-direction: column;
  gap: 12%;
}

/* Styling of overlay */
.bm-overlay {
  background: rgba(0, 0, 0, 0.3);
}

/* Individual item */
.bm-item {
  display: inline-block;
  font-weight: bold;
  text-align: center;
  text-decoration: none;
  transition: color 0.2s;
  position: relative;
  top: 2%;
}
.bm-item.menu-item {
  color: white;
}
.bm-item:hover {
  color: black;
}

@media screen and (max-width: 800px) {
  .bm-burger-bars {
      display: block;
    }
  .bm-burger-button {
    display: block;
  }
}