* {
    box-sizing: border-box;
}
body {
    margin: 0;
    font-family: 'Julius Sans One', sans-serif;
    margin: auto;
    background-color: #fffff0;
}
p {
    font-family: 'Roboto', sans-serif;
    font-size: 14px;
}
img {
    border-radius: 20px;
}
.center {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #fffff0;
    padding: 0 0 20px 0;
}
.flex-col {
    display: flex;
    flex-direction: column;
}

@media screen and (max-width: 1000px) {
    .center {
        padding: 20px;
    }
}